___CSS_LOADER_EXPORT___.push([module.id, "\n#view {\n    padding-left: 300px;\n    transition: all;\n    transition-duration: .3s;\n}\n#view.collapsed {\n    transition-duration: .5s;\n    padding-left: 80px;\n}\n.fade-enter-active,\n.fade-leave-active {\n  transition: opacity 0.2s ease;\n}\n.fade-enter-from,\n.fade-leave-active {\n  opacity: 0;\n}\n\n", "",{"version":3,"sources":["webpack://./resources/js/template/DefaultLayout.vue"],"names":[],"mappings":";AA8nBA;IACI,mBAAmB;IACnB,eAAe;IACf,wBAAwB;AAC5B;AAEA;IACI,wBAAwB;IACxB,kBAAkB;AACtB;AAEA;;EAEE,6BAA6B;AAC/B;AAEA;;EAEE,UAAU;AACZ","sourcesContent":["<template>\n    <div>\n        <vue-progress-bar></vue-progress-bar>\n\n        <div id=\"preloader\">\n            <div class=\"loader\"></div>\n        </div>\n\n        <sidebar-menu\n            class=\"sidebar\"\n            @update:collapsed=\"onCollapse\"\n            :menu=\"menu\"\n            width=\"290px\"\n            :showOneChild=\"true\"\n            theme='grey-theme'\n            @item-click=\"onItemClick\"\n        />\n\n        <div class=\"main-content\" id=\"view\" :class=\"[{'collapsed' : collapsed}]\">\n            <!-- header area start -->\n            <div class=\"header-area\">\n                <div class=\"row align-items-center\">\n                    <!-- nav and search button -->\n                    <div class=\"col-md-6 col-sm-8 clearfix\">\n                        <div class=\"search-box pull-left\">\n                            <form action=\"#\">\n                                <input type=\"text\" name=\"search\" placeholder=\"Search...\" required>\n                                <i class=\"ti-search\"></i>\n                            </form>\n                        </div>\n                    </div>\n                    <!-- profile info & task notification -->\n                    <div class=\"col-md-6 col-sm-4 clearfix\">\n                        <ul class=\"notification-area pull-right\">\n                            <li id=\"full-view\"><i class=\"ti-fullscreen\"></i></li>\n                            <li id=\"full-view-exit\"><i class=\"ti-zoom-out\"></i></li>\n                            <li class=\"dropdown\">\n                                <i class=\"ti-bell dropdown-toggle\" data-toggle=\"dropdown\">\n                                    <span>2</span>\n                                </i>\n                                <div class=\"dropdown-menu bell-notify-box notify-box\">\n                                    <span class=\"notify-title\">You have 3 new notifications <a href=\"#\">view all</a></span>\n                                    <div class=\"nofity-list\">\n                                        <a href=\"#\" class=\"notify-item\">\n                                            <div class=\"notify-thumb\"><i class=\"ti-key btn-danger\"></i></div>\n                                            <div class=\"notify-text\">\n                                                <p>You have Changed Your Password</p>\n                                                <span>Just Now</span>\n                                            </div>\n                                        </a>\n                                    </div>\n                                </div>\n                            </li>\n                            <li class=\"dropdown\">\n                                <i class=\"fa fa-envelope-o dropdown-toggle\" data-toggle=\"dropdown\"><span>3</span></i>\n                                <div class=\"dropdown-menu notify-box nt-enveloper-box\">\n                                    <span class=\"notify-title\">You have 3 new notifications <a href=\"#\">view all</a></span>\n                                    <div class=\"nofity-list\">\n                                        <a href=\"#\" class=\"notify-item\">\n                                            <div class=\"notify-thumb\">\n                                                <img src=\"assets/images/author/author-img1.jpg\" alt=\"image\">\n                                            </div>\n                                            <div class=\"notify-text\">\n                                                <p>Aglae Mayer</p>\n                                                <span class=\"msg\">Hey I am waiting for you...</span>\n                                                <span>3:15 PM</span>\n                                            </div>\n                                        </a>\n                                    </div>\n                                </div>\n                            </li>\n                            <li class=\"settings-btn\">\n                                <i class=\"ti-settings\"></i>\n                            </li>\n                            <li class=\"dropdown\">\n                                <i class=\"ti ti-user\" data-toggle=\"dropdown\"></i>\n\n                                <div class=\"dropdown-menu bell-notify-box notify-box\">\n                                    <span class=\"notify-title\">{{ user === null ? \"Loading\" : user.nom_personnel + ' ' + user.prenoms_personnel }} <a href=\"#\">Mon compte</a></span>\n                                    <div class=\"nofity-list\">\n                                        <a href=\"#\" @click=\"logOut()\" class=\"notify-item\">\n                                            <div class=\"notify-thumb\"><i class=\"fa fa-cog btn-primary\"></i></div>\n                                            <div class=\"notify-text\">\n                                                <p>Paramtres</p>\n                                                <span>Paramtres du compte</span>\n                                            </div>\n                                        </a>\n                                        <a href=\"#\" @click=\"logOut()\" class=\"notify-item\">\n                                            <div class=\"notify-thumb\"><i class=\"fa fa-sign-out btn-danger\"></i></div>\n                                            <div class=\"notify-text\">\n                                                <p>Se deconnecter</p>\n                                                <span>Quitter l'app</span>\n                                            </div>\n                                        </a>\n                                    </div>\n                                </div>\n                            </li>\n                        </ul>\n                    </div>\n                </div>\n            </div>\n\n            <router-view class=\"mt-5 ps-2 pe-2\" v-slot=\"{ Component }\">\n                <transition name=\"fade\" mode=\"out-in\">\n                    <component :is=\"Component\"></component>\n                </transition>\n            </router-view>\n        </div>\n\n        <footer>\n            <div class=\"footer-area\">\n                <p> Copyright 2018. All right reserved.</p>\n            </div>\n        </footer>\n\n        <div class=\"offset-area\">\n            <div class=\"offset-close\"><i class=\"ti-close\"></i></div>\n            <ul class=\"nav offset-menu-tab\">\n                <li><a class=\"active\" data-toggle=\"tab\" href=\"#activity\">Activity</a></li>\n                <li><a data-toggle=\"tab\" href=\"#settings\">Settings</a></li>\n            </ul>\n            <div class=\"offset-content tab-content\">\n                <div id=\"activity\" class=\"tab-pane fade in show active\">\n                    <div class=\"recent-activity\">\n                        <div class=\"timeline-task\">\n                            <div class=\"icon bg1\">\n                                <i class=\"fa fa-envelope\"></i>\n                            </div>\n                            <div class=\"tm-title\">\n                                <h4>Rashed sent you an email</h4>\n                                <span class=\"time\"><i class=\"ti-time\"></i>09:35</span>\n                            </div>\n                            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Esse distinctio itaque at.</p>\n                        </div>\n                    </div>\n                </div>\n                <div id=\"settings\" class=\"tab-pane fade\">\n                    <div class=\"offset-settings\">\n                        <h4>General Settings</h4>\n                        <div class=\"settings-list\">\n                            <div class=\"s-settings\">\n                                <div class=\"s-sw-title\">\n                                    <h5>Notifications</h5>\n                                    <div class=\"s-swtich\">\n                                        <input type=\"checkbox\" id=\"switch1\" />\n                                        <label for=\"switch1\">Toggle</label>\n                                    </div>\n                                </div>\n                                <p>Keep it 'On' When you want to get all the notification.</p>\n                            </div>\n                            <div class=\"s-settings\">\n                                <div class=\"s-sw-title\">\n                                    <h5>Show recent activity</h5>\n                                    <div class=\"s-swtich\">\n                                        <input type=\"checkbox\" id=\"switch2\" />\n                                        <label for=\"switch2\">Toggle</label>\n                                    </div>\n                                </div>\n                                <p>The for attribute is necessary to bind our custom checkbox with the input.</p>\n                            </div>\n                            <div class=\"s-settings\">\n                                <div class=\"s-sw-title\">\n                                    <h5>Show your emails</h5>\n                                    <div class=\"s-swtich\">\n                                        <input type=\"checkbox\" id=\"switch3\" />\n                                        <label for=\"switch3\">Toggle</label>\n                                    </div>\n                                </div>\n                                <p>Show email so that easily find you.</p>\n                            </div>\n                            <div class=\"s-settings\">\n                                <div class=\"s-sw-title\">\n                                    <h5>Show Task statistics</h5>\n                                    <div class=\"s-swtich\">\n                                        <input type=\"checkbox\" id=\"switch4\" />\n                                        <label for=\"switch4\">Toggle</label>\n                                    </div>\n                                </div>\n                                <p>The for attribute is necessary to bind our custom checkbox with the input.</p>\n                            </div>\n                            <div class=\"s-settings\">\n                                <div class=\"s-sw-title\">\n                                    <h5>Notifications</h5>\n                                    <div class=\"s-swtich\">\n                                        <input type=\"checkbox\" id=\"switch5\" />\n                                        <label for=\"switch5\">Toggle</label>\n                                    </div>\n                                </div>\n                                <p>Use checkboxes when looking for yes or no answers.</p>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script lang=\"ts\">\n\nimport axiosClient from '../axios';\nimport store from '../store';\nimport useAbility from '../services/AbilityServices';\nimport { defineComponent, Ref, ref } from 'vue';\n\ninterface User {\n    id: number | null,\n    role: number | null,\n    nom_personnel: string | null,\n    prenoms_personnel: string | null\n}\n\nconst { permissions, getPermissions }  = useAbility()\n\nconst logoImg = {\n    setup() {\n        return {\n            src: '/images/app-logo.png'\n        }\n    },\n    template: '<img :src=\"src\" class=\"w-100 mt-4 mb-4 pe-5 ps-5\"><hr class=\"bg-light ms-5 me-5\">'\n}\n\nexport default defineComponent({\n    setup() {\n        const collapsed: Ref<boolean> = ref(false);\n\n        return {\n            permissions,\n            getPermissions,\n            collapsed,\n        }\n    },\n\n    mounted() {\n        this.$Progress.finish();\n    },\n\n    created() {\n        this.$Progress.start();\n        this.$router.beforeEach((to, from, next) => {\n            axiosClient.get('abilities').then(response => {\n                this.$ability.update([\n                    { subject: 'all', action: response.data }\n                ])\n\n                if (to.meta.gate !== undefined && to.meta.gate !== null && this.$can(to.meta.gate) === false) {\n                    // Si pas de privilge necessaire\n                    this.$router.push('/403')\n                }\n            }).catch(err => {\n                console.error(\"Erreur ajax : \", err.response.data.message)\n            })\n\n            if (to.meta.progress !== undefined) {\n                let meta = to.meta.progress;\n                this.$Progress.parseMeta(meta);\n            }\n            this.$Progress.start();\n            next();\n        });\n\n        this.$router.afterEach((to, from) => {\n            this.$Progress.finish();\n        });\n    },\n\n    methods: {\n        onItemClick (event, item) {\n            if (item.logout === true) {\n                this.logOut();\n            }\n        },\n\n        /**\n         * Permet de deconnecter un utilisateur\n         *\n         * @return  {Promise}  Redirection permanente vers la page login\n         **/\n        async logOut(): Promise<any> {\n            await axiosClient.post('/auth/logout')\n            this.resetUser()\n            this.$router.push('/login')\n            window.location.href = '/login'\n        },\n\n\n        /**\n         * Si le menu se collapse\n         *\n         * @param   {boolean}  c  True or False\n         *\n         * @return  {void}\n         */\n        onCollapse(c: boolean): void {\n            this.collapsed = c;\n        },\n\n        /**\n         * Mettre l'utilisateur connect dans le store\n         *\n         * @return  {void}\n         */\n        resetUser(): void {\n            store.state.user.token = null\n            store.state.user.data = { id: null, role: null, nom_personnel: null, prenoms_personnel: null }\n            localStorage.removeItem('auth_token')\n        },\n    },\n\n    computed: {\n        /**\n         * Permet de detecter si un utilisateur est connect ou non\n         *\n         * @return  {Boolean}  True si connect, False sinon\n         */\n        isConnected(): boolean {\n            return store.state.user.token === null ? false : true;\n        },\n\n        /**\n         * Utilisateur connect\n         *\n         * @return  {User}  L'utilisateur connect\n         */\n        user(): User {\n            return store.getters.user\n        },\n\n        menu() {\n            return [\n                {\n                    header: true,\n                    hiddenOnCollapse: true,\n                    component: logoImg,\n                },\n                {\n                    href: '/dashboard',\n                    title: 'Dashboard',\n                    icon: 'fa fa-bar-chart'\n                },\n                {\n                    header: \"Dpot\",\n                    hidden: !this.$can('add_point_vente') && !this.$can('view_point_vente') && !this.$can('add_entrepot') && !this.$can('view_entrepot'),\n                    hiddenOnCollapse: true,\n                },\n                {\n                    title: 'Point de vente',\n                    icon: 'fa fa-home',\n                    hidden: !this.$can('add_point_vente') && !this.$can('view_point_vente'),\n                    child: [\n                        {\n                            title: 'Ajouter nouveau',\n                            href: '/point-de-vente/nouveau',\n                            icon: 'fa fa-plus',\n                            hidden: !this.$can('add_point_vente'),\n                        },\n                        {\n                            title: 'Liste',\n                            href: '/point-de-vente/liste',\n                            icon: 'fa fa-list',\n                            hidden: !this.$can('view_point_vente'),\n                        },\n                    ]\n                },\n                {\n                    title: 'Entrept',\n                    icon: 'fa fa-home',\n                    hidden: !this.$can('add_entrepot') && !this.$can('view_entrepot'),\n                    child: [\n                        {\n                            title: 'Ajouter nouveau',\n                            href: '/entrepot/nouveau',\n                            icon: 'fa fa-plus',\n                            hidden: !this.$can('add_entrepot'),\n                        },\n                        {\n                            title: 'Liste',\n                            href: '/entrepot/liste',\n                            icon: 'fa fa-list',\n                            hidden: !this.$can('view_entrepot'),\n                        },\n                    ]\n                },\n                // Menu pour la gestion de client et fournisseur\n                // -----------------------------------------------------------------------------------------------------\n                {\n                    header: \"Clients & Fournisseurs\",\n                    hiddenOnCollapse: true,\n                    hidden: false,\n                },\n                {\n                    title: 'Client',\n                    icon: 'fa fa-users',\n                    hidden: false,\n                    child: [\n                        {\n                            href: '/client/nouveau',\n                            title: 'Nouveau client',\n                            icon: 'fa fa-plus',\n                            class: 'fw-regular',\n                            // hidden: !this.$can('add_user'),\n                        },\n                        {\n                            href: '/client/liste',\n                            title: 'Liste des clients',\n                            icon: 'fa fa-list',\n                            // hidden: !this.$can('view_user'),\n                        },\n                        {\n                            title: 'Les catgories',\n                            icon: 'fa fa-tasks',\n                            child: [\n                                {\n                                    href: '/client/categorie/nouveau',\n                                    title: 'Nouveau',\n                                    icon: 'fa fa-plus',\n                                    class: 'fw-regular',\n                                    // hidden: !this.$can('add_user'),\n                                },\n                                {\n                                    href: '/client/categorie/liste',\n                                    title: 'Liste',\n                                    icon: 'fa fa-list',\n                                    class: 'fw-regular',\n                                    // hidden: !this.$can('add_user'),\n                                },\n                            ],\n                            // hidden: !this.$can('manage_roles_and_functions'),\n                        },\n                    ]\n                },\n                {\n                    title: 'Fournisseurs',\n                    icon: 'fa fa-users',\n                    hidden: false,\n                    child: [\n                        {\n                            href: '/fournisseur/nouveau',\n                            title: 'Nouveau fournisseur',\n                            icon: 'fa fa-plus',\n                            class: 'fw-regular',\n                            // hidden: !this.$can('add_user'),\n                        },\n                        {\n                            href: '/fournisseur/liste',\n                            title: 'Liste des fournisseurs',\n                            icon: 'fa fa-list',\n                            // hidden: !this.$can('view_user'),\n                        },\n                        {\n                            title: 'Les catgories',\n                            icon: 'fa fa-tasks',\n                            child: [\n                                {\n                                    href: '/fournisseur/categorie/nouveau',\n                                    title: 'Nouveau',\n                                    icon: 'fa fa-plus',\n                                    class: 'fw-regular',\n                                    // hidden: !this.$can('add_user'),\n                                },\n                                {\n                                    href: '/fournisseur/categorie/liste',\n                                    title: 'Liste',\n                                    icon: 'fa fa-list',\n                                    class: 'fw-regular',\n                                    // hidden: !this.$can('add_user'),\n                                },\n                            ],\n                            // hidden: !this.$can('manage_roles_and_functions'),\n                        },\n                    ]\n                },\n                // -----------------------------------------------------------------------------------------------------\n                // Menu pour la gestion de l'article\n                // -----------------------------------------------------------------------------------------------------\n                {\n                    header: \"Article\",\n                    hiddenOnCollapse: true,\n                    hidden: false,\n                },\n                {\n                    title: 'Article',\n                    icon: 'fa fa-gift',\n                    hidden: false,\n                    child: [\n                        {\n                            href: '/article/nouveau',\n                            title: 'Nouveau article',\n                            icon: 'fa fa-plus',\n                            class: 'fw-regular',\n                            // hidden: !this.$can('add_user'),\n                        },\n                        {\n                            href: '/article/liste',\n                            title: 'Liste des article',\n                            icon: 'fa fa-list',\n                            // hidden: !this.$can('view_user'),\n                        },\n                        {\n                            title: 'Les catgories',\n                            icon: 'fa fa-tasks',\n                            child: [\n                                {\n                                    href: '/article/categorie/nouveau',\n                                    title: 'Nouveau',\n                                    icon: 'fa fa-plus',\n                                    class: 'fw-regular',\n                                    // hidden: !this.$can('add_user'),\n                                },\n                                {\n                                    href: '/article/categorie/liste',\n                                    title: 'Liste',\n                                    icon: 'fa fa-list',\n                                    class: 'fw-regular',\n                                    // hidden: !this.$can('add_user'),\n                                },\n                            ],\n                            // hidden: !this.$can('manage_roles_and_functions'),\n                        },\n                    ]\n                },\n                {\n                    title: 'Dvis',\n                    icon: 'fa fa-file',\n                    hidden: false,\n                    child: [\n                        {\n                            href: '/devis/fournisseur/liste',\n                            title: 'Approvisionnement',\n                            icon: 'fa fa-arrow-right',\n                            // hidden: !this.$can('view_devis'),\n                        },\n                        {\n                            href: '/devis/client/liste',\n                            title: 'Vente',\n                            icon: 'fa fa-arrow-left',\n                            // hidden: !this.$can('view_devis'),\n                        },\n                    ]\n                },\n                // --------------------------------------------------------------\n                {\n                    title: 'Commande',\n                    icon: 'fa fa-file',\n                    hidden: false,\n                    child: [\n                        {\n                            href: '/commande/fournisseur/liste',\n                            title: 'Approvisionnement',\n                            icon: 'fa fa-arrow-right',\n                            // hidden: !this.$can('view_commande'),\n                        },\n                        {\n                            href: '/commande/client/liste',\n                            title: 'Vente',\n                            icon: 'fa fa-arrow-left',\n                            // hidden: !this.$can('view_commande'),\n                        },\n                    ]\n                },\n                // ---------------------------------------------------------------------------------------\n                {\n                    header: \"Personnel\",\n                    hiddenOnCollapse: true,\n                    hidden: !this.$can('manage_roles_and_functions') && !this.$can('view_user') && !this.$can('add_user'),\n                },\n                {\n                    title: 'Personnel',\n                    icon: 'fa fa-users',\n                    hidden: !this.$can('manage_roles_and_functions') && !this.$can('view_user') && !this.$can('add_user'),\n                    child: [\n                        {\n                            href: '/personnel/nouveau',\n                            title: 'Nouveau personnel',\n                            icon: 'fa fa-plus',\n                            class: 'fw-regular',\n                            hidden: !this.$can('add_user'),\n                        },\n                        {\n                            href: '/personnel/liste',\n                            title: 'Liste des personnel',\n                            icon: 'fa fa-list',\n                            hidden: !this.$can('view_user'),\n                        },\n                        {\n                            href: '/personnel/fonctions',\n                            title: 'Les fonctions & rles',\n                            icon: 'fa fa-tasks',\n                            hidden: !this.$can('manage_roles_and_functions'),\n                        },\n                    ]\n                },\n                {\n                    header: \"Paramtres\",\n                    hiddenOnCollapse: true,\n                    hidden: !this.$can('manage_settings'),\n                },\n                {\n                    title: 'Paramtres',\n                    icon: 'fa fa-cog',\n                    hidden: !this.$can('manage_settings'),\n                    child: [\n                        {\n                            href: '/point-de-vente/liste',\n                            title: 'Dvise',\n                            icon: 'fa fa-money',\n                        },\n                        {\n                            href: '/parametres/entreprise',\n                            title: 'Infos de l\\'entreprise',\n                            icon: 'fa fa-info-circle',\n                        },\n                    ]\n                },\n                {\n                    header: \"Mon compte\",\n                    hiddenOnCollapse: true,\n                },\n                {\n                    href: '/point-de-vente/liste',\n                    title: 'Paramtres',\n                    icon: 'fa fa-cog',\n                },\n                {\n                    title: 'Dconnexion',\n                    logout: true,\n                    icon: 'fa fa-sign-out',\n                },\n            ];\n        }\n    }\n});\n</script>\n\n\n<style>\n\n#view {\n    padding-left: 300px;\n    transition: all;\n    transition-duration: .3s;\n}\n\n#view.collapsed {\n    transition-duration: .5s;\n    padding-left: 80px;\n}\n\n.fade-enter-active,\n.fade-leave-active {\n  transition: opacity 0.2s ease;\n}\n\n.fade-enter-from,\n.fade-leave-active {\n  opacity: 0;\n}\n\n</style>\n"],"sourceRoot":""}]);
